import close from './assets/close.svg';
import logo from './assets/logo.png';
import minimize from './assets/minimize.svg';
import React, { useState, useRef } from 'react';
import Countdown from 'react-countdown';

// Import Tauri APIs
import { invoke } from '@tauri-apps/api/core';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();

// Utility to detect Tauri environment
const isTauri = (): boolean =>
  typeof window !== 'undefined' &&
  (window as any).__TAURI_IPC__ !== undefined;

interface Payload {
  app_version: string;
  prompt_timer: string;
  minimise_option: boolean;
  question_p_1: string;
  deviceId: string;
  prompt_id: string;
  answer_options: string | string[];
  error_log?: string;
  prompt_group?: string;
}

export function Riddle({ payload }: { payload: Payload }) {
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showContent, setShowContent] = useState(false);
  const countdownRef = useRef<any>(null);

  const closeClick = () => {
    if (isTauri()) {
      appWindow.close();
    } else {
      console.warn('closeClick called outside Tauri');
    }
  };

  const minimizeClick = () => {
    if (isTauri()) {
      appWindow.minimize();
    } else {
      console.warn('minimizeClick called outside Tauri');
    }
  };

  const handleClick = (id: string, deviceId: string, questionId: string) =>
    async () => {
      setLoading(true);
      setError(null);
      try {
        let url: string;
        const config = isTauri()
          ? (await invoke('get_config')) as { demo: boolean; local_dev?: boolean }
          : { demo: false, local_dev: false };

        logsSend(`Quiz Riddle, questionId : ${questionId}`)();

        url = config.demo
          ? 'http://144.126.229.130/demo/record_data'
          : 'http://144.126.229.130/record_data';

        const resp = await fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ answer: id, device_id: deviceId, question_id: questionId }),
        });

        if (!resp.ok) throw new Error(`HTTP error! Status: ${resp.status}`);
        setData(await resp.json());

        if (isTauri()) appWindow.close();
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

  const renderer = ({ completed }: { completed: boolean }) => {
    if (completed) minimizeClick();
    return null;
  };

  const handleShowContent = () => {
    setShowContent(true);
    countdownRef.current?.start();
  };

  const showVersion = () => {
    document.getElementById('app_version')?.setAttribute('style', 'display:block');
  };

  const answerOptions: string[] = Array.isArray(payload.answer_options)
    ? payload.answer_options
    : payload.answer_options
    ? JSON.parse(payload.answer_options)
    : [];

  const logsSend = (answer = 'n/a') => async () => {
    try {
      const config = isTauri()
        ? (await invoke('get_config')) as { demo: boolean; local_dev: boolean }
        : { demo: false, local_dev: false };

      const baseUrl = config.demo
        ? config.local_dev
          ? 'http://localhost:5000/demo/logs/record_data'
          : 'http://144.126.229.130/demo/logs/record_data'
        : 'http://144.126.229.130/logs/record_data';

      const body = JSON.stringify({
        device_id: payload.deviceId,
        prompt_id: payload.prompt_id || 'n/a',
        answer,
        recieved_status: 'true',
        error_log: payload.error_log || 'n/a',
      });

      console.log('logs send body:', body);

      const resp = await fetch(baseUrl, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body,
      });

      if (!resp.ok) throw new Error(`HTTP error! Status: ${resp.status}`);
      console.log('logs send result:', await resp.json());
    } catch (err) {
      console.log('logs send error:', err);
    }
  };

  return (
    <div className="window">
      {/* ... rest of JSX unchanged */}
    </div>
  );
}
