import React, { useState, useEffect } from 'react';
import minimize from './assets/minimize.svg';
import logo from './assets/logo.png';

import { Math as MathComponent_ } from './math';
import { Joke } from './joke';
import { Riddle } from './riddle';
import { Survey } from './survey';
import { NoQuestion } from './noquestion';
import { CreateCustomer } from './createCustomer';

import { invoke } from '@tauri-apps/api/core';
import { getVersion } from '@tauri-apps/api/app';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();

import { Payload } from './types';

export function Quiz() {
  const [data, setData] = useState<Payload | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const minimizeClick = () => appWindow.minimize();

  const renderer = ({ total, completed }: { total: number; completed: boolean }) => {
    if (completed) minimizeClick();
    return <span><b>{Math.round(total / 1000)}s</b></span>;
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [deviceId, app_version, update_available, config] = await Promise.all([
          invoke<string>('get_machine_id'),
          getVersion(),
          invoke<{ is_update_available: boolean; progress?: number }>('update_available').catch(() => ({ is_update_available: false })),
          invoke<{ demo: boolean; local_dev: boolean }>('get_config'),
        ]);

        const baseUrl = config.demo
          ? config.local_dev
            ? `http://localhost:5000/demo?device_id=${encodeURIComponent(deviceId)}`
            : `http://144.126.229.130/demo?device_id=${encodeURIComponent(deviceId)}`
          : `http://144.126.229.130?device_id=${encodeURIComponent(deviceId)}`;

        const res = await fetch(baseUrl);
        if (!res.ok) throw new Error(`HTTP error! Status: ${res.status}`);

        const raw = await res.json();

        const payload: Payload = {
          deviceId,
          app_version,
          update_available,
          prompt_type: raw.prompt_type || '',
          prompt_timer: raw.prompt_timer || '',
          minimise_option: raw.minimise_option,
          question_p_1: raw.question_p_1 || '',
          question_p_2: raw.question_p_2 || '',
          Answer: raw.Answer || '',
          question_type: raw.question_type || '',
          prompt_id: raw.prompt_id || '',
          error_log: raw.error_log || '',
          prompt_group: raw.prompt_group || '',
          answer_options: raw.answer_options || [],
        };

        setData(payload);
      } catch (err) {
        setError((err as Error).message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const renderContent = () => {
    if (loading) {
      return (
        <div className="window">
          <div className="content">
            <p>Loading quiz...</p>
          </div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="window">
          <div className="content">
            <p style={{ color: 'red' }}>Error: {error}</p>
            <p>Please restart the application or check your network connection.</p>
          </div>
        </div>
      );
    }

    if (!data) return null;

    if (data.update_available?.is_update_available) {
      return (
        <div className="window">
          <div className="content update_notification">
            <h1>Update Available</h1>
            <p><b>An update is available. The application is downloading the update package in the background.</b></p>
            {data.update_available.progress !== undefined && (
              <p>Progress: {data.update_available.progress}%</p>
            )}
          </div>
        </div>
      );
    }

    switch (data.prompt_type) {
      case 'customer_register':
        return <CreateCustomer payload={data} />;
      case 'riddle_gen':
        return <Riddle payload={data} />;
      case 'maths_q_gen':
        return <MathComponent_ payload={data} />;
      case 'joke_gen':
      case 'joke_dep':
        return <Joke payload={data} />;
      case 'survey_q_gen':
      case 'survey_q_dep':
        return <Survey payload={data} />;
      case 'na':
        return null;
      default:
        return <NoQuestion payload={data} />;
    }
  };

  return <>{renderContent()}</>;
}
