import React, { useState } from 'react';
import './App.css';
import { invoke } from '@tauri-apps/api/core';

const isTauri = typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;


export function LeaderVoice() {
  const [masterEmail, setMasterEmail] = useState('');
  const [accessCode, setAccessCode] = useState('');
  const [time, setTime] = useState('');
  const [date, setDate] = useState('');
  const [popupText, setPopupText] = useState('');
  const [popupType, setPopupType] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const settings = {
      masterEmail,
      accessCode,
      time,
      date,
      popupText,
      popupType,
    };

    if (isTauri) {
      try {
        const result = await invoke('save_leader_voice_settings', { settings });
        console.log('Settings saved in backend:', result);
        alert('Settings sent to backend.');
      } catch (err) {
        console.error('Failed to send settings:', err);
        alert('Failed to save settings in backend.');
      }
    } else {
      // For web fallback or dev
      alert(
        `popup_text: ${popupText}\npopup_type: ${popupType}\ntime: ${time}\ndate: ${date}`
      );
    }
  };

  return (
    <div
      className="window"
      style={{ fontFamily: 'Nunito, sans-serif', background: '#F2F2F2', padding: 20 }}
    >
      <h2>Leader Voice</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Master Email Address:</label>
          <input
            type="email"
            value={masterEmail}
            onChange={(e) => setMasterEmail(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Access Code:</label>
          <input
            type="password"
            value={accessCode}
            onChange={(e) => setAccessCode(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Time:</label>
          <input
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Date:</label>
          <input
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Popup Text:</label>
          <input
            type="text"
            value={popupText}
            onChange={(e) => setPopupText(e.target.value)}
          />
        </div>
        <div>
          <label>Popup Type:</label>
          <input
            type="text"
            value={popupType}
            onChange={(e) => setPopupType(e.target.value)}
          />
        </div>
        <button className="save-button" type="submit">
          Save Settings
        </button>
      </form>
    </div>
  );
}
