import close from './assets/close.svg';
import logo from './assets/logo.png';
import minimize from './assets/minimize.svg';
import happy_face from './assets/happyface.svg';
import neutral_face from './assets/neutralface.svg';
import unhappy_face from './assets/unhappyface.svg';
import Countdown from 'react-countdown';
import React, { useState } from 'react';

// Tauri imports
import { invoke } from '@tauri-apps/api/core';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();

// Tauri detection helper
const isTauri = () =>
  typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;

interface Payload {
  app_version: string;
  prompt_timer: string;
  minimise_option: boolean;
  question_p_1: string;
  deviceId: string;
  prompt_id: string;
  error_log?: string;
  prompt_group?: string;
}

export function Survey({ payload }: { payload: Payload }) {
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const closeClick = () => {
    if (isTauri()) appWindow.close();
    else console.warn('closeClick called outside Tauri');
  };

  const minimizeClick = () => {
    if (isTauri()) appWindow.minimize();
    else console.warn('minimizeClick called outside Tauri');
  };

  const handleClick = (id: string) => async () => {
    setLoading(true);
    setError(null);
    try {
      const config = isTauri()
        ? (await invoke('get_config')) as { demo: boolean; local_dev: boolean }
        : { demo: false, local_dev: false };

      logsSend(`Quiz Survey, questionId: ${payload.prompt_id}`)();

      const url = config.demo
        ? config.local_dev
          ? 'http://localhost:5000/demo/record_data'
          : 'http://144.126.229.130/demo/record_data'
        : 'http://144.126.229.130/record_data';

      const resp = await fetch(url, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          answer: id,
          device_id: payload.deviceId,
          question_id: payload.prompt_id
        }),
      });

      if (!resp.ok) throw new Error(`HTTP error! Status: ${resp.status}`);
      setData(await resp.json());
      if (isTauri()) appWindow.close();
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const renderer = ({ completed }: { total: number; completed: boolean }) => {
    if (completed) minimizeClick();
    return null;
  };

  const showVersion = () => {
    document.getElementById('app_version')?.setAttribute('style', 'display:block');
  };

  const logsSend = (answer = 'n/a') => async () => {
    try {
      const config = isTauri()
        ? (await invoke('get_config')) as { demo: boolean; local_dev: boolean }
        : { demo: false, local_dev: false };

      const baseUrl = config.demo
        ? config.local_dev
          ? 'http://localhost:5000/demo/logs/record_data'
          : 'http://144.126.229.130/demo/logs/record_data'
        : 'http://144.126.229.130/logs/record_data';

      const body = JSON.stringify({
        device_id: payload.deviceId,
        prompt_id: payload.prompt_id || 'n/a',
        answer,
        recieved_status: 'true',
        error_log: payload.error_log || 'n/a',
      });

      console.log('logs send body:', body);
      const resp = await fetch(baseUrl, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body,
      });
      if (!resp.ok) throw new Error(`HTTP error! Status: ${resp.status}`);
      console.log('logs send result:', await resp.json());
    } catch (err) {
      console.log('logs send error:', err);
    }
  };

  return (
    <div className='window'>
      <div className="header">
        <div className="left logo_timer">
          <div className="app_version" id='app_version'>{payload.app_version}</div>
          <img className="logo" src={logo} alt="" onClick={showVersion} />
          <Countdown
            className="left timer"
            date={Date.now() + parseInt(payload.prompt_timer, 10) * 1000}
            renderer={renderer}
          />
        </div>

        <div className="right window_control">
          {payload.minimise_option && (
            <img onClick={minimizeClick} src={minimize} alt="" />
          )}
          <img onClick={closeClick} className="close_align" src={close} alt="" />
        </div>
      </div>

      <div className='content'>
        <p>{payload.question_p_1}</p>
      </div>

      <div className="centered-container">
        <div className="container">
          {[unhappy_face, neutral_face, happy_face].map((src, idx) => (
            <div key={idx} className='image-container'>
              <img
                onClick={handleClick((idx + 1).toString())}
                className='image'
                src={src}
                alt=""
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}