import minimize from './assets/minimize.svg';
import logo from './assets/logo.png';
import React, { useState } from 'react';

// Import Tauri APIs
import { invoke } from '@tauri-apps/api/core';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();

// Tauri environment check
const isTauri = typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;

export function CreateCustomer({ payload }: { payload: any }) {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [companyInput, setCompanyInput] = useState('');
  const [departmentInput, setDepartmentInput] = useState('');

  const minimizeClick = async () => {
    if (isTauri) {
      await appWindow.minimize();
    }
  };

  const handleCompanyInputChange = (event: any) => {
    setCompanyInput(event.target.value);
  };

  const handleDepartmentInputChange = (event: any) => {
    setDepartmentInput(event.target.value);
  };

  const createCustomerRequest = (deviceId: string, company: string, department: string) => {
    return async function () {
      setLoading(true);
      setError(null);

      try {
        let config: any = { demo: false };

        // If in Tauri, invoke Rust backend
        if (isTauri) {
          config = await invoke('get_config');
        }

        const url = config.demo
          ? 'http://144.126.229.130/demo/register_customer'
          : 'http://144.126.229.130/register_customer';

        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            device_id: deviceId,
            company,
            department,
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const result = await response.json();
        setData(result);

        // Close the window only in Tauri
        if (isTauri) {
          await appWindow.close();
        }

      } catch (err) {
        setError((err as any).message);
      } finally {
        setLoading(false);
      }
    };
  };

  const showVersion = () => {
    const el = document.getElementById('app_version');
    if (el) {
      el.style.display = 'block';
    }
  };

  return (
    <div className='window'>
      <div className="header">
        <div className="left logo_timer">
          <div className="app_version" id='app_version'>
            v{payload?.app_version ?? 'N/A'}
          </div>
          <img className="logo" src={logo} alt="Logo" onClick={showVersion} />
        </div>
        <div className="right window_control">
          <img className="close_align" onClick={minimizeClick} src={minimize} alt="Minimize" />
        </div>
      </div>

      <div className="dialog">
        <div className='dialog-content'>
          <label htmlFor="company-name">Company name:</label>
          <input
            required
            type="text"
            value={companyInput}
            onChange={handleCompanyInputChange}
          />
          <label htmlFor="department-name">Department name:</label>
          <input
            required
            type="text"
            value={departmentInput}
            onChange={handleDepartmentInputChange}
          />
          <button
            className='save-button'
            onClick={createCustomerRequest(payload.deviceId, companyInput, departmentInput)}
            disabled={loading}
          >
            {loading ? 'Saving...' : 'Save'}
          </button>
          {error && <p className="error-message">{error}</p>}
        </div>
      </div>
    </div>
  );
}
