import { createRoot } from 'react-dom/client';
import App from './App';

// Tauri event API imports (used conditionally)
import { listen, emit } from '@tauri-apps/api/event';

// Runtime check for Tauri environment
const isTauri = typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;

// Mount React root
const container = document.getElementById('root') as HTMLElement;
const root = createRoot(container);
root.render(<App />);

// Setup Tauri event listeners only if running in Tauri
if (isTauri) {
  (async () => {
    try {
      const unlisten = await listen<string>('ipc-example', (event) => {
        console.log('Received event from backend:', event.payload);
        // Stop listening after the first message
        unlisten();
      });

      // Emit an event to the backend
      emit('ipc-example', { message: 'ping' });

    } catch (err) {
      console.error('Tauri event error:', err);
    }
  })();
}
