import { MemoryRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { Quiz } from './quiz';
import { Users } from './Users';
import { LeaderVoice } from './LeaderVoice';
import { Login } from './Login';
import React, { useState } from 'react';
import './App.css';

// Tauri APIs (conditionally used)
import { invoke } from '@tauri-apps/api/core';

// Runtime check for Tauri environment
const isTauri = typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;

function Main() {
  return (
    <div>
      <nav style={{ marginBottom: 16 }}>
        <Link to="/">Quiz</Link> |{' '}
        <Link to="/users">Users</Link> |{' '}
        <Link to="/leader-voice">Leader Voice</Link>
      </nav>
      <Quiz />
    </div>
  );
}

export default function App() {
  const [loggedIn, setLoggedIn] = useState(false);
  const [loginError, setLoginError] = useState<string | null>(null);

  const handleLogin = async (email: string, code: string) => {
    setLoginError(null);

    if (!isTauri) {
      setLoginError('Tauri environment not detected.');
      return;
    }

    try {
      const result = await invoke('check_login', { email, password: code });
      if (result === true) {
        setLoggedIn(true);
      } else {
        setLoginError('Invalid credentials.');
      }
    } catch (e: any) {
      setLoginError(e.message || 'Login failed.');
    }
  };

  if (!loggedIn) {
    return <Login onLogin={handleLogin} error={loginError} />;
  }

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/users" element={<Users />} />
        <Route path="/leader-voice" element={<LeaderVoice />} />
      </Routes>
    </Router>
  );
}
