import close from './assets/close.svg';
import logo from './assets/logo.png';
import minimize from './assets/minimize.svg';
import Countdown from 'react-countdown';
import React, { useEffect } from 'react';

// Tauri API imports
import { invoke } from '@tauri-apps/api/core';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();


interface Payload {
  deviceId: string;
  app_version: string;
  prompt_timer: string;
  minimise_option: boolean;
  question_p_1: string;
  question_p_2: string;
  prompt_id?: string;
  error_log?: string;
  prompt_group?: string;
}

const isTauri = typeof (window as any).__TAURI_IPC__ !== undefined && !!(window as any).__TAURI_IPC__;

export function Joke({ payload }: { payload: Payload }) {
  // Minimize the app window safely
  const minimizeClick = async () => {
    if (isTauri) {
      await appWindow.minimize();
    }
  };

  // Close the app window safely
  const closeClick = async () => {
    if (isTauri) {
      await appWindow.close();
    }
  };

  // Show version label
  const show_version = () => {
    const appVersionElement = document.getElementById('app_version');
    if (appVersionElement) {
      appVersionElement.style.display = 'block';
    }
  };

  // Send logs to the backend if running in Tauri
  const logs_send = (answer: string = 'Quiz : Joke') => {
    return async function () {
      try {
        if (!isTauri) {
          console.warn('Skipping log send  not running in Tauri');
          return;
        }

        const config = await invoke('get_config') as {
          demo: boolean;
          local_dev: boolean;
        };

        let url: string;
        if (config.demo) {
          url = config.local_dev
            ? 'http://localhost:5000/demo/logs/record_data'
            : 'http://144.126.229.130/demo/logs/record_data';
        } else {
          url = 'http://144.126.229.130/logs/record_data';
        }

        const body = JSON.stringify({
          device_id: payload.deviceId,
          prompt_id: payload.prompt_id || 'n/a',
          answer: answer,
          recieved_status: 'true',
          error_log: payload.error_log || 'n/a',
        });

        console.log('logs send body:', body);

        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body,
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const result = await response.json();
        console.log('logs send result:', result);
      } catch (error) {
        console.error('logs send error:', (error as Error).message);
      }
    };
  };

  // Send logs on mount
  useEffect(() => {
    logs_send()();
  }, []);

  // Countdown renderer
  const renderer = ({
    total,
    completed,
  }: {
    total: number;
    completed: boolean;
  }) => {
    if (completed) {
      minimizeClick();
    }
    return null;
  };

  return (
    <div className="window">
      <div className="header">
        <div className="left logo_timer">
          <div className="app_version" id="app_version">
            {payload.app_version}
          </div>
          <img className="logo" src={logo} alt="logo" onClick={show_version} />
          <Countdown
            className="left logo_timer"
            date={Date.now() + parseInt(payload.prompt_timer) * 1000}
            renderer={renderer}
          />
        </div>

        <div className="right window_control">
          {payload.minimise_option && (
            <img onClick={minimizeClick} src={minimize} alt="minimize" />
          )}
          <img
            onClick={closeClick}
            className="close_align"
            src={close}
            alt="close"
          />
        </div>
      </div>

      <div className="content">
        <p>{payload.question_p_1}</p>
      </div>
      <div className="content">
        <p>
          <b>{payload.question_p_2}</b>
        </p>
      </div>
    </div>
  );
}
