import React, { useState } from 'react';
import './App.css';
import { invoke } from '@tauri-apps/api/core';

export function Login({ onLogin, error }: { onLogin: (email: string, code: string) => void, error?: string | null }) {
  const [masterEmail, setMasterEmail] = useState('');
  const [accessCode, setAccessCode] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onLogin(masterEmail, accessCode);
  };

  return (
    <div className="window" style={{ fontFamily: 'Nunito, sans-serif', background: '#F2F2F2' }}>
      <h2>Login</h2>
      {error && <div style={{ color: 'red', marginBottom: 12, textAlign: 'center' }}>{error}</div>}
      <form onSubmit={handleSubmit}>
        <div>
          <label>Master Email Address:</label>
          <input type="email" value={masterEmail} onChange={(e: React.ChangeEvent<HTMLInputElement>) => setMasterEmail(e.target.value)} required />
        </div>
        <div>
          <label>Access Code:</label>
          <input type="password" value={accessCode} onChange={(e: React.ChangeEvent<HTMLInputElement>) => setAccessCode(e.target.value)} required />
        </div>
        <button className="save-button" type="submit">Login</button>
      </form>
    </div>
  );
}

async function handleLogin(email: string, password: string) {
  try {
    const result = await invoke('check_login', { email, password });
    if (result) {
      // Login successful
    } else {
      // Login failed
    }
  } catch (error) {
    // Handle error
  }
}
