import React, { useState, useRef } from 'react';
import Countdown from 'react-countdown';
import close from './assets/close.svg';
import logo from './assets/logo.png';
import minimize from './assets/minimize.svg';
import { invoke } from '@tauri-apps/api/core';
import { Window } from '@tauri-apps/api/window';

const appWindow = Window.getCurrent();

interface Payload {
  app_version: string;
  prompt_timer: string;
  minimise_option: boolean;
  question_p_1: string;
  deviceId: string;
  prompt_id: string;
  error_log?: string;
  prompt_group?: string;
}

export function Math({ payload }: { payload: Payload }) {
  const [mathInput, setMathInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const countdownRef = useRef(null);

  const minimizeClick = () => appWindow.minimize();
  const closeClick = () => appWindow.close();

  const showVersion = () => {
    const versionEl = document.getElementById('app_version');
    if (versionEl) versionEl.style.display = 'block';
  };

  const logSend = (answer = 'n/a') => {
    return async () => {
      try {
        const config = await invoke('get_config') as { demo: boolean; local_dev?: boolean };

        const url = config.demo
          ? config.local_dev
            ? 'http://localhost:5000/demo/logs/record_data'
            : 'http://144.126.229.130/demo/logs/record_data'
          : 'http://144.126.229.130/logs/record_data';

        const body = JSON.stringify({
          device_id: payload.deviceId,
          prompt_id: payload.prompt_id || 'n/a',
          answer,
          recieved_status: 'true',
          error_log: payload.error_log || 'n/a',
        });

        await fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body,
        });
      } catch (err) {
        console.error('Log send error:', (err as Error).message);
      }
    };
  };

  const handleSubmit = () => {
    return async () => {
      setLoading(true);
      setError(null);

      try {
        const config = await invoke('get_config') as { demo: boolean; local_dev?: boolean };

        await logSend(`Quiz Maths, input: ${mathInput}, questionId: ${payload.prompt_id}`)();

        const url = config.demo
          ? 'http://144.126.229.130/demo/record_data'
          : 'http://144.126.229.130/record_data';

        const res = await fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            answer: mathInput,
            device_id: payload.deviceId,
            question_id: payload.prompt_id,
          }),
        });

        if (!res.ok) throw new Error(`HTTP error! Status: ${res.status}`);
        await res.json();

        appWindow.close(); // Close on success
      } catch (err) {
        setError((err as Error).message);
      } finally {
        setLoading(false);
      }
    };
  };

  const renderer = ({ completed }: { completed: boolean }) => {
    if (completed) minimizeClick();
    return null;
  };

  return (
    <div className="window">
      <div className="header">
        <div className="left logo_timer">
          <div className="app_version" id="app_version">
            {payload.app_version}
          </div>
          <img className="logo" src={logo} alt="logo" onClick={showVersion} />
          <Countdown
            className="left timer"
            date={Date.now() + parseInt(payload.prompt_timer) * 1000}
            renderer={renderer}
          />
        </div>

        <div className="right window_control">
          {payload.minimise_option && (
            <img onClick={minimizeClick} src={minimize} alt="minimize" />
          )}
          <img onClick={closeClick} className="close_align" src={close} alt="close" />
        </div>
      </div>

      <div className="math-content">
        <p>{payload.question_p_1}</p>
        <input
          required
          type="text"
          value={mathInput}
          onChange={(e) => setMathInput(e.target.value)}
        />
        <button className="submit-button" onClick={handleSubmit()}>
          {loading ? 'Submitting...' : 'Submit'}
        </button>
        {error && <div className="error-text">{error}</div>}
      </div>
    </div>
  );
}
