import React, { useEffect, useState } from 'react';
import './App.css';

// Tauri import
import { invoke } from '@tauri-apps/api/core';

// Utility to detect Tauri environment
const isTauri = () =>
  typeof window !== 'undefined' && (window as any).__TAURI_IPC__ !== undefined;

interface User {
  id: number;
  name: string;
  email: string;
}

export function Users() {
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [editId, setEditId] = useState<number | null>(null);

  const fetchUsers = async () => {
    setLoading(true);
    setError(null);
    if (!isTauri()) {
      console.warn('Skipping fetchUsers: not running inside Tauri');
      setLoading(false);
      return;
    }
    try {
      const result = await invoke('get_users');
      setUsers(result as User[]);
    } catch (e: any) {
      setError(e.message || 'Failed to fetch users');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const handleAdd = async () => {
    if (!isTauri()) {
      console.warn('Skipping add_user: not running inside Tauri');
      return;
    }
    if (!name || !email) return;
    try {
      await invoke('add_user', { name, email });
      setName('');
      setEmail('');
      fetchUsers();
    } catch (e: any) {
      setError(e.message || 'Failed to add user');
    }
  };

  const handleUpdate = async () => {
    if (!isTauri()) {
      console.warn('Skipping update_user: not running inside Tauri');
      return;
    }
    if (editId === null) return;
    try {
      await invoke('update_user', { id: editId, name, email });
      setEditId(null);
      setName('');
      setEmail('');
      fetchUsers();
    } catch (e: any) {
      setError(e.message || 'Failed to update user');
    }
  };

  const handleDelete = async (id: number) => {
    if (!isTauri()) {
      console.warn('Skipping delete_user: not running inside Tauri');
      return;
    }
    try {
      await invoke('delete_user', { id });
      fetchUsers();
    } catch (e: any) {
      setError(e.message || 'Failed to delete user');
    }
  };

  return (
    <div style={{ padding: 24, background: '#F2F2F2', minHeight: '100vh', fontFamily: 'Nunito, sans-serif', color: '#222' }}>
      <h2>Users</h2>
      {error && <div style={{ color: 'red', marginBottom: 8 }}>{error}</div>}
      {/* Form */}
      {/* ... */}
      {loading ? <div>Loading...</div> : (
        <table> {/* table markup unchanged */}
          {/* ... */}
        </table>
      )}
    </div>
  );
}